@import 'styles/mixins';
@import 'styles/variables';

.create {
  &-title {
    width: 100%;
    @include flex();
  }
  &-subtitle {
    font-family: $font-family;
    font-size: 11px;
    line-height: 13px;
    text-align: center;
    letter-spacing: 1.65px;
    font-weight: bold;
    color: $color-black;
    text-transform: uppercase;
    margin-top: 1rem;
  }
  &-target {
    @include flex(column, space-between);
    margin-top: 1rem;
    gap: 1rem;
    height: 100%;
    width: 90%;
    &-form {
      width: 80%;
    }
  }
}

.topic-item {
  @include flex(row, flex-start);
  gap: 1rem;
  .icon {
    @include flex();
    width: 1rem;
    .img {
      max-height: 1rem;
    }
  }
}

.smilies-small {
  max-height: 30px;
  margin-top: 0.5rem;
}
